.CityCard{display:inline-block;position:relative;left:10px;height:107px;width:160px;background-size:cover;margin:0 20px 10px 0px;border-radius:4px;box-shadow:0 3px 8px 0 rgba(0,0,0,0.3),0 0 1px 0 rgba(0,0,0,0.3);transition:box-shadow .2s ease}.CityCard:hover{box-shadow:0 6px 8px 0 rgba(0,0,0,0.4),0 0 2px 0 rgba(0,0,0,0.4)}.CityCard__wrapper{height:100%;width:100%;background-image:linear-gradient(166deg, #fff 40%, rgba(255,255,255,0) 68%);border-radius:4px}.CityCard h3{position:absolute;top:14px;left:14px;font-size:18px;font-weight:700;color:#151515}
